<template>
  <div class="container p-4">
    <form @submit.prevent="submit">
      <div class="card m-4">
        <div class="card-header">Créer un compte</div>
        <div class="card-body">
          <div class="text-center text-warning" v-if="errorMessage">
            {{ errorMessage }}
            <hr />
            <button class="btn btn-block btn-warning" @click="tray_again" type="button">Réessayer</button>
          </div>
          <loading-compoenent v-else-if="isLoading" />
          <div v-else>
            <div class="form-group py-1">
              <input type="email" v-model="user.email" class="form-control" placeholder="Email *" />
            </div>
            <div class="form-group py-1">
              <input
                type="password"
                v-model="user.password"
                class="form-control"
                placeholder="Password *"
              />
            </div>
          </div>
          <page-viewer-component initial-file="cgu.html" v-show="false" ref="cgu" />
        </div>
        <div class="card-footer" v-if="!errorMessage && !isLoading">
          <div class="d-grid p-1">
            <button class="btn btn-primary btn-lg" type="submit">
              <i class="bi-person me-2" />Suivant
            </button>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { auth } from "../firebase";

export default {
  name: "inscriptionDistributeurComponent",
  components: {},
  data() {
    return {
      user: {
        email: "7mmdmo7@gmail.com",
        password: "7mmdmo7"
      },
      errorMessage: null,
      isLoading: false
    };
  },
  created() {},
  methods: {
    tray_again() {
      this.isLoading = false;
      this.errorMessage = null;
    },
    submit() {
      console.log(this.$refs.cgu);
      console.log(this.$refs.cgu.rawHtml);
      this.$swal({
        title: "Condition Génrale d'Utilisation",
        icon: "info",
        html: this.$refs.cgu.rawHtml,
        width: "95%",
        showCloseButton: true,
        showCancelButton: true,
        focusConfirm: true,
        confirmButtonText:
          '<i class="bi-check"></i> Lu et Accepter les conditions générale d’utilisation',
        cancelButtonText: "No!"
      }).then(result => {
        if (result.isConfirmed) {
          this.ok_submit();
        }
      });
    },
    ok_submit() {
      this.isLoading = true;
      this.errorMessage = null;
      auth
        .createUserWithEmailAndPassword(this.user.email, this.user.password)
        .then(userCredential => {
          this.isLoading = false;
          this.errorMessage = null;
          console.log("User successfully created!", userCredential);
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = error;
          console.log("error", error);
          this.$swal({
            title: "Ouaps!",
            text: error.message,
            icon: "error",
            confirmButtonText: "Tray again"
          });
        });
    }
  }
};
</script>

